import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, message, Checkbox } from 'antd';
import { getSemesterById, updateSemester, createSemester } from 'services/semesterService';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    label: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formzone' })
class FormSemesterPage extends Component {
  state = {
    semester: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ semester: true, semesterId: match.params.id });
      this.getDataById(match.params.id);
    }
  };

  getDataById = async id => {
    const res = await getSemesterById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { semesterId } = this.state;
    try {
      if (!semesterId) {
        await createSemester(values);
        message.success('Tạo thành công');
      } else {
        await updateSemester({ id: semesterId, ...values });
        message.success('Chỉnh sửa thành công');
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { semester } = this.state;
    return (
      <Card title={`${semester ? 'Cập nhật' : 'Thêm'} khu vực`}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item
              hasFeedback
              style={{ display: 'none' }}
              label="Mã học kỳ"
              {...formItemLayout}
            >
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                // rules: [
                //   {
                //     required: true,
                //     message: 'Vui lòng nhập thông tin!',
                //   },
                // ],
              })(<Input disabled={semester} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên học kỳ" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Học kỳ chính">
              {getFieldDecorator('isMain', {
                initialValue: initialValue.isMain,
                valuePropName: 'checked',
              })(<Checkbox />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormSemesterPage {...props} />
  </Page>
));



